
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import eventBus from '../service/eventbus';
import { setStore, getStore } from '../../config/mUtils'
import { cityData3 } from '../../config/cityDate'
import { getUserAddressList, saveAddress, defaultAddress, delAddress} from '../../service/getData'
export default {
directives: {
TransferDom
},
data() {
return {
title: '地址管理',
isempty: false,
ischeck: false,
list: [],
radiobox:'',
id:0,
show:false
}
},
created() {

},
components: {
Toast,
XHeader,
XButton,
Group,
Cell,
CellBox,
CheckIcon,
Checker,
CheckerItem,
Confirm
},
props: [],
filters: {









addres: function(address, proStr, cityStr, regionStr){
return proStr+cityStr+regionStr+address
}
},
mounted() {
this.$vux.loading.show({
text: '加载中...'
})

getUserAddressList(getStore('token')).then(resolved => {
if(resolved.msgCode == '0000') {
if(resolved.data.length == 0) {
this.isempty = true
}
this.list = resolved.data
resolved.data.forEach((ele)=>{
if(ele.isDefault)
this.radiobox = ele.id
})
this.$nextTick(() => {
this.$vux.loading.hide()
})
console.log(JSON.stringify(resolved.data))
} else {
this.$nextTick(() => {
this.$vux.loading.hide()
this.$vux.toast.show({
text: resolved.msg,
type:'cancel'
})
})
}
})
setTimeout(()=>{
this.$nextTick(() => {
this.$vux.loading.hide()
})
},5000)
},
methods: {
onCancel() {
console.log(11)
},
onConfirm(){
this.$vux.toast.show({
text: '删除成功'
})
delAddress(this.id, getStore('token')).then(resolved => {
if(resolved.msgCode == '0000') {
this.list.forEach(function(element,index) {
if(this.id===element.id){
this.list.splice(index,1)
}
}, this);
console.log('删除成功'+this.id)
} else {
this.$nextTick(() => {
this.$vux.toast.show({
text: resolved.msg,
type:'cancel'
})
})
}
})
},

onItemClick(id,status){
console.log(status)
defaultAddress(id,status,getStore('token')).then(resolved => {
console.log(JSON.stringify(resolved))
if(resolved.msgCode == '0000') {
console.log('成功'+id)
} else {
this.$nextTick(() => {
this.$vux.toast.show({
text: resolved.msg,
type:'cancel'
})
})
}
})
console.log('发送设置默认请求操作'+id)
},

add() {
this.$router.push({
name: 'AddressAdd',
query: {
type: 'add'
}
})
},

del(id){
if(this.radiobox==id){
return this.$vux.toast.show({
text: '默认地址不可删除',
type:'cancel',
width:'11em'
})
}
this.id = id
this.show=true
},

edit(id){
this.$router.push({name:'AddressAdd', query: { id: id, type: 'edit' }})
},
isSelected(id, name, mobile, address) {
if(this.$route.query.type!=='list'){

eventBus.$emit('address', {id:id, name:name, mobile:mobile, address:address});
setStore('ischange', true)
setStore('address', {id:id, name:name, mobile:mobile, address:address})

this.$router.go(-1);
}
}
}
}



